import { Component, ViewChild } from "@angular/core"
import { NavController } from "ionic-angular"

import { Slides, AlertController, Events } from "ionic-angular"

import {
  ThemeableBrowser,
  ThemeableBrowserOptions,
  ThemeableBrowserObject
} from "@ionic-native/themeable-browser"

import { Http } from "@angular/http"
import { HttpClient } from "@angular/common/http"
import { calcBindingFlags } from "@angular/core/src/view/util"

import { MyApp } from "../../app/app.component"

@Component({
  selector: "page-home",
  templateUrl: "home.html"
})
export class HomePage {
  @ViewChild("mySlider")
  slides: Slides

  isConnected = MyApp.connection

  options: ThemeableBrowserOptions = {
    statusbar: {
      color: "#ffffffff"
    },
    toolbar: {
      height: 0,
      color: "#f0f0f0ff"
    },
    title: {
      color: "#003264ff",
      showPageTitle: false
    },
    backButton: {
      image: "back",
      imagePressed: "back_pressed",
      align: "left",
      event: "backPressed"
    },
    forwardButton: {
      image: "forward",
      imagePressed: "forward_pressed",
      align: "left",
      event: "forwardPressed"
    },
    closeButton: {
      image: "close",
      imagePressed: "close_pressed",
      align: "left",
      event: "closePressed"
    },
    customButtons: [
      {
        image: "share",
        imagePressed: "share_pressed",
        align: "right",
        event: "sharePressed"
      }
    ],
    menu: {
      image: "menu",
      imagePressed: "menu_pressed",
      title: "Test",
      cancel: "Cancel",
      align: "right",
      items: [
        {
          event: "helloPressed",
          label: "Hello World!"
        },
        {
          event: "testPressed",
          label: "Test!"
        }
      ]
    },
    backButtonCanClose: false
  }

  link_url_adlux = "http://www.sejaadlux.com.br"

  banners: any

  premium_links = [
    {
      title: "Treinamentos",
      icon: "trending-up",
      url: `${this.link_url_adlux}/treinamentos`
    },
    {
      title: "Graduações",
      icon: "school",
      url: `${this.link_url_adlux}/graduacoes`
    },
    {
      title: "Serviços",
      icon: "megaphone",
      url: `${this.link_url_adlux}/servicos`
    },
    {
      title: "Solicitações",
      icon: "images",
      url: `${this.link_url_adlux}/minhas-solicitacoes`
    },
    {
      title: "Recrutador ADLUX",
      icon: "people",
      url: `${this.link_url_adlux}/recrutador`
    },
    {
      title: "Artigos Exclusivos",
      icon: "paper",
      url: `${this.link_url_adlux}/artigos-exclusivos`
    }
  ]
  constructor(
    public navCtrl: NavController,
    private themeableBrowser: ThemeableBrowser,
    public http: Http,
    public httpClient: HttpClient,
    public alertCtrl: AlertController,
    private events: Events
  ) {
    this.events.subscribe("user:disconnected", () => {
      this.slides.update()
    })

    this.events.subscribe("user:getBanners", () => {
      this.http.get(`${this.link_url_adlux}/public_api/banners`).subscribe(
        res => {
          this.banners = res.json()
          this.slides.update()
        },
        err => {
          console.log(err)
        }
      )
    })

    this.http.get(`${this.link_url_adlux}/public_api/banners`).subscribe(
      res => {
        this.banners = res.json()
        this.slides.update()
      },
      err => {
        console.log(err)
      }
    )
  }

  openBrowser(url) {
    if (MyApp.connection) {
      if (url) {
        const browser: ThemeableBrowserObject = this.themeableBrowser.create(
          url,
          "_blank",
          this.options
        )
      }
    } else {
      let alert = this.alertCtrl.create({
        title: "Ops! Sem conexão",
        subTitle:
          "Infelizmente voce está sem conexão com a internet. Conecte-se para que voce possa acessar estes canais",
        buttons: ["Ok, entendi"]
      })
      alert.present()
    }
  }

  ionViewDidEnter() {
    this.slides.update()
  }
}



