import { Component, ViewChild } from "@angular/core"
import { Platform, AlertController, Nav, Events } from "ionic-angular"
import { StatusBar } from "@ionic-native/status-bar"
import { SplashScreen } from "@ionic-native/splash-screen"
import { Network } from "@ionic-native/network"

import { HomePage } from "../pages/home/home"

@Component({
  templateUrl: "app.html"
})
export class MyApp {
  @ViewChild(Nav)
  nav: Nav

  rootPage: any = HomePage

  static connection: boolean = true

  constructor(
    public platform: Platform,
    statusBar: StatusBar,
    splashScreen: SplashScreen,
    public alertCtrl: AlertController,
    private events: Events,
    public network: Network
  ) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault()
      splashScreen.hide()

      console.log("test")
      let type = this.network.type
      if (type == "unknown" || type == "none" || type == undefined) {
        MyApp.connection = false
      } else {
        MyApp.connection = true
      }

      this.verifyConnection()

      this.platform.registerBackButtonAction(() => {
        if (this.nav.canGoBack()) {
          this.nav.pop()
        } else {
          this.myHandlerFunction()
        }
      })
    })
  }

  myHandlerFunction() {
    let confirm = this.alertCtrl.create({
      title: "Sair",
      message: "Deseja realmente sair do app?",
      buttons: [
        {
          text: "Não sair",
          handler: () => {}
        },
        {
          text: "Sair",
          handler: () => {
            navigator["app"].exitApp()
          }
        }
      ]
    })
    confirm.present()
  }

  displayNetworkUpdate(connectionState: string, timer: number) {
    if (connectionState == "offline") {
      let toast = document.createElement("div")
      toast.setAttribute("class", "offline-toast")
      toast.innerHTML = "Você está offline"
      document.querySelector("ion-app").appendChild(toast)
      this.events.publish("user:disconnected")
    } else {
      this.events.publish("user:getBanners")
      let toasts = document.querySelectorAll(".offline-toast")
      ;[].filter.call(toasts, function(element) {
        element.remove()
      })
    }
  }

  verifyConnection() {
    // watch network for a disconnect
    this.network.onDisconnect().subscribe(data => {
      MyApp.connection = false

      this.displayNetworkUpdate(data.type, 9999999999999999)

      let body = document.getElementsByTagName("ng-component")[0]
      body.classList.add("offline")
    })

    this.network.onConnect().subscribe(data => {
      MyApp.connection = true

      this.displayNetworkUpdate(data.type, 1)

      let body = document.getElementsByTagName("ng-component")[0]
      body.classList.remove("offline")
    })
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component)
  }
}



